
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.btn-detail {
	margin-right: 10px;
}

div.debug {
  border: 1px solid red;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.server-detail,
.server-player {
  margin: 0px auto;
  text-align: center;
}
.server-info .info-item {
  display: inline-block;
}
.info-table {
  border-collapse: separate;
  border-spacing: 15px 2px;
  margin-right: 30px;
}
.map-preview {
  vertical-align: top;
  margin-top: 20px;
}
.detail-split {
  border: none;
  height: 1px;
  background-color: #ccc;
  margin: 30px 0px;
}
.player-info {
  display: table;
  margin: 0px auto;
  margin-top: 50px;
  width: 60%;
}
.player-info thead {
  border-top: 1px solid #ccc;
}
